.container {
    width: 350px;
    height: 250px;
    border: 2px solid #6EE4FF;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.count {
    width: 320px;
    height: 130px;
    box-sizing: border-box;
    margin-bottom: 15px;
    background-color: #6EE4FF;
    border-radius: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 4rem;
    font-weight: bold;
    color: #2A2C36;
    user-select: none;
    text-align: center;
}

.countRed {
    color: red;
}

.incorrectValCount {
    background-color: #2A2C36;
    border: 2px solid #6EE4FF;
    font-size: 1.9rem;
    font-weight: 600;
    color: red;
}

.enterValue {
    background-color: #2A2C36;
    border: 2px solid #6EE4FF;
    font-size: 1.3rem;
    font-weight: 600;
    color: #6EE4FF;
}

.buttons {
    width: 320px;
    height: 70px;
    border: 2px solid #6EE4FF;
    border-radius: 10px;
    display: flex;
    justify-content: space-around;
    align-items: center;
}

.button {
    font-size: 2rem;
    font-weight: bold;
    padding: 4px 15px 4px 15px;
    border-radius: 8px;
    color: #2A2C36;
    background-color: #6EE4FF;
    border: none;
    cursor: pointer;
    user-select: none;
}

.button:active {
    color: #6EE4FF;
    background-color: #252B36;
}

.incButton {
    font-size: 2rem;
    font-weight: bold;
    padding: 4px 15px 4px 15px;
    border-radius: 8px;
    color: #2A2C36;
    background-color: #6EE4FF;
    border: none;
    cursor: pointer;
    user-select: none;
}

.incButton:active {
    color: #6EE4FF;
    background-color: #252B36;
}

.resetButton {
    font-size: 2rem;
    font-weight: bold;
    padding: 4px 15px 4px 15px;
    border-radius: 8px;
    color: #2a2c36;
    background-color: #6EE4FF;
    border: none;
    cursor: pointer;
    user-select: none;
}

.resetButton:active {
    color: #6EE4FF;
    background-color: #252b36;
}

.disabled {
    font-size: 2rem;
    font-weight: bold;
    padding: 4px 15px 4px 15px;
    border-radius: 8px;
    color: #252B36;
    background-color: #457686;
    border: none;
}